
$card-width: 200px;
$columns: 4;
$gap-x: 48px;

.page-content {
  font-family: "Roboto", Arial, sans-serif;
}

// .page-content__wrap {
.pc-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  width: $card-width * $columns + $gap-x * ($columns - 1);
  margin: 0 auto;
}

.product-card--wrap {
  margin: 50px 0 0 $gap-x;
}

.product-card--wrap:nth-last-child(-n + 4) {
  margin: 46px 0 0 $gap-x;
}

.product-card--wrap:nth-child(4n + 1) {
  margin-left: 0;
}
